"use strict"

jQuery(document).ready(function ($) {

    // tabs

    let tab = function () {
        let tabLink = document.querySelectorAll(".tab-js .item");
        let tabContent = document.querySelectorAll(".tabs__content-js");
        let tabName;
        tabLink.forEach((item) => {
            item.addEventListener("click", selsectTabLink);
        });
        function selsectTabLink() {
            tabLink.forEach((item) => {
                item.classList.remove("active");
            });
            this.classList.add("active");
            tabName = this.getAttribute("datat-tab-name");
            selectTabContent(tabName);
        }
        const selectTabContent = (tabName) => {
            tabContent.forEach((item) => {
                item.classList.contains(tabName)
                    ? item.classList.add("active")
                    : item.classList.remove("active");
            });
        };
    };
    tab();

    // loyality__calculator

    $("#calculator__range-js").on("input change", function () {
        let $value = $(this).val()
        $(".result-number").html($value)
    })

    $(document).on("change", function () {
        let selectVal = $("#calculator__select-js option:selected").val()
        let output = $(".content__items-left--bottom .number")
        output.html(selectVal)
    })

    // js-select
    // const select = document.querySelector('#calculator__select-js');
    // new Choices(select, {
    //     searchEnabled: false,
    //     itemSelectText: '',
    //     classNames: {

    //     }
    // });


})