$(document).ready(function(){
    $(".polzunok-1").slider({
	    min: 0,
	    max: 100,
	    step: 5,
	    range: "min",
	    animate: "slow",
	    slide : function(event, ui) {
			$(".installment__calculator-sub-value-1").html(ui.value);
	    }
	});
    $(".polzunok-2").slider({
	    min: 0,
	    max: 100,
	    step: 5,
	    range: "min",
	    animate: "slow",
	    slide : function(event, ui) {
			$(".installment__calculator-sub-value-2").html(ui.value);
	    }
	});

});